/**
 * ë©”ëª¨ë¦¬ ì‚¬ìš©ëŸ‰ ë¡œê¹… ë° ë¶„ì„ ìœ í‹¸ë¦¬í‹°
 * 
 * ì¥ê¸°ì ì¸ ë©”ëª¨ë¦¬ ì‚¬ìš© íŒ¨í„´ ë¶„ì„ê³¼ ìµœì í™” ì „ëµ ìˆ˜ë¦½ì— í•„ìš”í•œ
 * ë°ì´í„°ë¥¼ ìˆ˜ì§‘í•˜ê³  ì €ì¥í•©ë‹ˆë‹¤.
 */

import { getMemoryInfo } from '../memory-management';
import { getPerformanceHistory } from '../performance-metrics';
import { normalizeMemoryInfo } from './format-utils';
// MemoryEventTypeì„ src/typesì—ì„œ ê°€ì ¸ì˜¤ëŠ” ëŒ€ì‹  enum-convertersì—ì„œ ê°€ì ¸ì˜´
import { MemoryEventType } from '../enum-converters';
import { MemoryInfo } from '@/types';

/**
 * ë©”ëª¨ë¦¬ ë¡œê±° ëª¨ë“ˆ
 * 
 * ë©”ëª¨ë¦¬ ì‚¬ìš©ëŸ‰ ë° ê´€ë ¨ ì´ë²¤íŠ¸ë¥¼ ë¡œê¹…í•˜ê¸° ìœ„í•œ ìœ í‹¸ë¦¬í‹°
 */

// ë¡œê·¸ ë ˆë²¨ ì •ì˜
export enum LogLevel {
  DEBUG = 0,
  INFO = 1,
  WARN = 2,
  ERROR = 3
}

// ë¡œê·¸ í•­ëª© ì¸í„°í˜ì´ìŠ¤
export interface LogEntry {
  timestamp: number;
  level: LogLevel;
  message: string;
  data?: Record<string, unknown>;
}

// ë¡œê·¸ ì„¤ì • ì¸í„°í˜ì´ìŠ¤
export interface LoggerOptions {
  minLevel?: LogLevel;
  maxLogs?: number;
  printToConsole?: boolean;
  moduleName?: string;
}

// ê¸°ë³¸ ë¡œê±° ì˜µì…˜
const DEFAULT_OPTIONS: LoggerOptions = {
  minLevel: LogLevel.INFO,
  maxLogs: 100,
  printToConsole: true,
  moduleName: 'memory'
};

/**
 * ë©”ëª¨ë¦¬ ë¡œê±° í´ë˜ìŠ¤
 */
export class MemoryLogger {
  private logs: LogEntry[] = [];
  private options: LoggerOptions;

  constructor(options: LoggerOptions = {}) {
    this.options = { ...DEFAULT_OPTIONS, ...options };
  }

  /**
   * ë¡œê·¸ ê¸°ë¡
   * @param level ë¡œê·¸ ë ˆë²¨
   * @param message ë©”ì‹œì§€
   * @param data ì¶”ê°€ ë°ì´í„°
   */
  private log(level: LogLevel, message: string, data?: Record<string, unknown>): void {
    // ì„¤ì •ëœ ìµœì†Œ ë ˆë²¨ë³´ë‹¤ ë‚®ì€ ë¡œê·¸ëŠ” ë¬´ì‹œ
    if (level < this.options.minLevel!) {
      return;
    }

    // ë¡œê·¸ í•­ëª© ìƒì„±
    const entry: LogEntry = {
      timestamp: Date.now(),
      level,
      message,
      data
    };

    // ë¡œê·¸ ë°°ì—´ì— ì¶”ê°€
    this.logs.push(entry);

    // ë¡œê·¸ ê°œìˆ˜ ì œí•œ
    if (this.logs.length > this.options.maxLogs!) {
      this.logs.shift();
    }

    // ì½˜ì†” ì¶œë ¥ (ì„¤ì •ëœ ê²½ìš°)
    if (this.options.printToConsole) {
      this.printToConsole(entry);
    }
  }

  /**
   * ì½˜ì†”ì— ë¡œê·¸ ì¶œë ¥
   * @param entry ë¡œê·¸ í•­ëª©
   */
  private printToConsole(entry: LogEntry): void {
    const timestamp = new Date(entry.timestamp).toISOString();
    const prefix = this.options.moduleName ? `[${this.options.moduleName}]` : '';

    switch (entry.level) {
      case LogLevel.DEBUG:
        console.debug(`${timestamp} ${prefix} ${entry.message}`, entry.data);
        break;
      case LogLevel.INFO:
        console.info(`${timestamp} ${prefix} ${entry.message}`, entry.data);
        break;
      case LogLevel.WARN:
        console.warn(`${timestamp} ${prefix} ${entry.message}`, entry.data);
        break;
      case LogLevel.ERROR:
        console.error(`${timestamp} ${prefix} ${entry.message}`, entry.data);
        break;
    }
  }

  /**
   * ë””ë²„ê·¸ ë¡œê·¸ ê¸°ë¡
   * @param message ë©”ì‹œì§€
   * @param data ì¶”ê°€ ë°ì´í„°
   */
  debug(message: string, data?: Record<string, unknown>): void {
    this.log(LogLevel.DEBUG, message, data);
  }

  /**
   * ì •ë³´ ë¡œê·¸ ê¸°ë¡
   * @param message ë©”ì‹œì§€
   * @param data ì¶”ê°€ ë°ì´í„°
   */
  info(message: string, data?: Record<string, unknown>): void {
    this.log(LogLevel.INFO, message, data);
  }

  /**
   * ê²½ê³  ë¡œê·¸ ê¸°ë¡
   * @param message ë©”ì‹œì§€
   * @param data ì¶”ê°€ ë°ì´í„°
   */
  warn(message: string, data?: Record<string, unknown>): void {
    this.log(LogLevel.WARN, message, data);
  }

  /**
   * ì˜¤ë¥˜ ë¡œê·¸ ê¸°ë¡
   * @param message ë©”ì‹œì§€
   * @param data ì¶”ê°€ ë°ì´í„°
   */
  error(message: string, data?: Record<string, unknown>): void {
    this.log(LogLevel.ERROR, message, data);
  }

  /**
   * ëª¨ë“  ë¡œê·¸ ê°€ì ¸ì˜¤ê¸°
   */
  getLogs(): LogEntry[] {
    return [...this.logs];
  }

  /**
   * íŠ¹ì • ë ˆë²¨ ì´ìƒì˜ ë¡œê·¸ë§Œ ê°€ì ¸ì˜¤ê¸°
   * @param level ìµœì†Œ ë¡œê·¸ ë ˆë²¨
   */
  getLogsByLevel(level: LogLevel): LogEntry[] {
    return this.logs.filter(entry => entry.level >= level);
  }

  /**
   * ì˜¤ë¥˜ ë¡œê·¸ë§Œ ê°€ì ¸ì˜¤ê¸°
   */
  getErrorLogs(): LogEntry[] {
    return this.logs.filter(entry => entry.level === LogLevel.ERROR);
  }
}

// ê¸°ë³¸ ë¡œê±° ì¸ìŠ¤í„´ìŠ¤
export const memoryLogger = new MemoryLogger();
export const logger = memoryLogger; // í¸ì˜ë¥¼ ìœ„í•œ ë³„ì¹­

// í¸ì˜ì„±ì„ ìœ„í•œ ê¸°ë³¸ í•¨ìˆ˜ ë‚´ë³´ë‚´ê¸°
export const debug = memoryLogger.debug.bind(memoryLogger);
export const info = memoryLogger.info.bind(memoryLogger);
export const warn = memoryLogger.warn.bind(memoryLogger);
export const error = memoryLogger.error.bind(memoryLogger);

// ë©”ëª¨ë¦¬ ë¡œê·¸ ì—”íŠ¸ë¦¬ ì¸í„°í˜ì´ìŠ¤
export interface MemoryLogEntry {
  timestamp: number;
  info: MemoryInfo;
  eventType: MemoryEventType;
  eventDescription?: string;
  componentId?: string;
  route?: string;
}

// ë©”ëª¨ë¦¬ ì‚¬ìš© í†µê³„ ì¸í„°í˜ì´ìŠ¤
export interface MemoryUsageStats {
  averageUsage: number;
  peakUsage: number;
  minUsage: number;
  lastUsage: number;
  usageOverTime: Array<{ timestamp: number; usageMB: number }>;
  optimizationEvents: Array<{ timestamp: number; freedMemory: number }>;
  gcEvents: Array<{ timestamp: number; freedMemory: number }>;
  leakSuspects: Array<{ componentId: string; frequency: number }>;
}

// ëˆ„ë½ëœ ë³€ìˆ˜ì™€ ìƒìˆ˜ ì •ì˜ ì¶”ê°€
const memoryLogs: MemoryLogEntry[] = [];
const MAX_LOG_ENTRIES = 1000;
const MEMORY_LOG_DB = 'memory_log_db';
const INDEXEDDB_STORE = 'memory_logs';

/**
 * ë©”ëª¨ë¦¬ ì‚¬ìš©ëŸ‰ ë¡œê¹…
 * @param eventType ì´ë²¤íŠ¸ íƒ€ì…
 * @param eventDescription ì´ë²¤íŠ¸ ì„¤ëª… (ì„ íƒ)
 * @param componentId ì»´í¬ë„ŒíŠ¸ ID (ì„ íƒ)
 * @param route í˜„ì¬ ë¼ìš°íŠ¸ (ì„ íƒ)
 */
export async function logMemoryUsage(
  eventType: MemoryEventType,
  eventDescription?: string,
  componentId?: string,
  route?: string
): Promise<MemoryLogEntry> {
  // í˜„ì¬ ë©”ëª¨ë¦¬ ì •ë³´ ê°€ì ¸ì˜¤ê¸°
  const rawMemoryInfo = await getMemoryInfo() || {
    heap_used: 0,
    heap_total: 0,
    heap_used_mb: 0,
    heapUsed: 0,
    heapTotal: 0,
    heapUsedMB: 0,
    rss: 0,
    rss_mb: 0,
    rssMB: 0,
    percent_used: 0,
    percentUsed: 0,
    timestamp: Date.now()
  };

  // í‘œì¤€í™”ëœ MemoryInfo ì¸í„°í˜ì´ìŠ¤ë¡œ ë³€í™˜
  const standardizedInfo = normalizeMemoryInfo(rawMemoryInfo as any);

  // í˜„ì¬ ë¼ìš°íŠ¸ ê°€ì ¸ì˜¤ê¸° (route ë§¤ê°œë³€ìˆ˜ê°€ ì—†ëŠ” ê²½ìš°)
  if (!route && typeof window !== 'undefined') {
    route = window.location.pathname;
  }

  // ë¡œê·¸ í•­ëª© ìƒì„±
  const logEntry: MemoryLogEntry = {
    timestamp: Date.now(),
    info: standardizedInfo,
    eventType,
    eventDescription,
    componentId,
    route
  };

  // ì¸ë©”ëª¨ë¦¬ ë¡œê·¸ì— ì¶”ê°€
  memoryLogs.push(logEntry);

  // ìµœëŒ€ ê°œìˆ˜ ì œí•œ
  if (memoryLogs.length > MAX_LOG_ENTRIES) {
    memoryLogs.shift();
  }

  // IndexedDBì— ì €ì¥ (ë°±ê·¸ë¼ìš´ë“œë¡œ ì‹¤í–‰)
  saveLogToIndexedDB(logEntry).catch(err =>
    console.error('ë©”ëª¨ë¦¬ ë¡œê·¸ ì €ì¥ ì¤‘ ì˜¤ë¥˜:', err)
  );

  return logEntry;
}

/**
 * ì£¼ê¸°ì ì¸ ë©”ëª¨ë¦¬ ëª¨ë‹ˆí„°ë§ ì‹œì‘
 * @param intervalMs ì²´í¬ ê°„ê²© (ë°€ë¦¬ì´ˆ)
 * @returns ëª¨ë‹ˆí„°ë§ ì¤‘ì§€ í•¨ìˆ˜
 */
export function startMemoryMonitoring(intervalMs: number = 60000): () => void {
  const intervalId = setInterval(() => {
    logMemoryUsage(MemoryEventType.PERIODIC_CHECK);
  }, intervalMs);

  return () => clearInterval(intervalId);
}

/**
 * IndexedDBì— ë¡œê·¸ ì €ì¥
 * @param logEntry ë¡œê·¸ í•­ëª©
 */
async function saveLogToIndexedDB(logEntry: MemoryLogEntry): Promise<void> {
  return new Promise((resolve, reject) => {
    // IndexedDBê°€ ì‚¬ìš© ê°€ëŠ¥í•œì§€ í™•ì¸
    if (!window.indexedDB) {
      reject(new Error('IndexedDBë¥¼ ì§€ì›í•˜ì§€ ì•ŠëŠ” ë¸Œë¼ìš°ì €ì…ë‹ˆë‹¤'));
      return;
    }

    // ë°ì´í„°ë² ì´ìŠ¤ ì—´ê¸°
    const request = window.indexedDB.open(MEMORY_LOG_DB, 1);

    // ë°ì´í„°ë² ì´ìŠ¤ ìƒì„±/ì—…ê·¸ë ˆì´ë“œ ì´ë²¤íŠ¸
    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result;

      // ê°ì²´ ì €ì¥ì†Œê°€ ì—†ìœ¼ë©´ ìƒì„±
      if (!db.objectStoreNames.contains(INDEXEDDB_STORE)) {
        db.createObjectStore(INDEXEDDB_STORE, { keyPath: 'timestamp' });
      }
    };

    // ì˜¤ë¥˜ ì²˜ë¦¬
    request.onerror = (event) => {
      reject(new Error(`IndexedDB ì˜¤ë¥˜: ${(event.target as IDBOpenDBRequest).error}`));
    };

    // ì„±ê³µ ì²˜ë¦¬
    request.onsuccess = (event) => {
      const db = (event.target as IDBOpenDBRequest).result;

      try {
        // íŠ¸ëœì­ì…˜ ì‹œì‘
        const transaction = db.transaction([INDEXEDDB_STORE], 'readwrite');
        const store = transaction.objectStore(INDEXEDDB_STORE);

        // ë¡œê·¸ í•­ëª© ì €ì¥
        const addRequest = store.add(logEntry);

        addRequest.onsuccess = () => {
          resolve();
        };

        addRequest.onerror = () => {
          reject(new Error(`ë¡œê·¸ í•­ëª© ì €ì¥ ì‹¤íŒ¨: ${addRequest.error}`));
        };

        // íŠ¸ëœì­ì…˜ ì™„ë£Œ ì´ë²¤íŠ¸
        transaction.oncomplete = () => {
          db.close();
        };
      } catch (error) {
        reject(error);
      }
    };
  });
}

/**
 * ì €ì¥ëœ ë©”ëª¨ë¦¬ ë¡œê·¸ ê°€ì ¸ì˜¤ê¸°
 * @param limit ê°€ì ¸ì˜¬ ìµœëŒ€ í•­ëª© ìˆ˜
 * @param startTime ì‹œì‘ ì‹œê°„ (ë°€ë¦¬ì´ˆ)
 * @param endTime ì¢…ë£Œ ì‹œê°„ (ë°€ë¦¬ì´ˆ)
 * @param eventTypes í•„í„°ë§í•  ì´ë²¤íŠ¸ íƒ€ì… ë°°ì—´
 * @returns ë¡œê·¸ í•­ëª© ë°°ì—´
 */
export async function getMemoryLogs(
  limit: number = 100,
  startTime?: number,
  endTime?: number,
  eventTypes?: MemoryEventType[]
): Promise<MemoryLogEntry[]> {
  // ì‹œê°„ ê¸°ì¤€ í•„í„°ë§
  const _now = Date.now(); // ì‚¬ìš©í•˜ì§€ ì•ŠëŠ” ë³€ìˆ˜ì— _ ì¶”ê°€
  const filteredByTime = memoryLogs.filter((log: MemoryLogEntry) => {
    if (startTime && log.timestamp < startTime) return false;
    if (endTime && log.timestamp > endTime) return false;
    return true;
  });

  // ì´ë²¤íŠ¸ íƒ€ì… ê¸°ì¤€ í•„í„°ë§
  const filtered = eventTypes
    ? filteredByTime.filter((log: MemoryLogEntry) => eventTypes.includes(log.eventType as MemoryEventType))
    : filteredByTime;

  // ìµœì‹  ìˆœìœ¼ë¡œ ì •ë ¬í•˜ê³  ì œí•œëœ ê°œìˆ˜ ë°˜í™˜
  return filtered
    .sort((a: MemoryLogEntry, b: MemoryLogEntry) => b.timestamp - a.timestamp)
    .slice(0, limit);
}

/**
 * ë©”ëª¨ë¦¬ ì‚¬ìš© í†µê³„ ë¶„ì„
 * @param startTime ì‹œì‘ ì‹œê°„ (ë°€ë¦¬ì´ˆ)
 * @param endTime ì¢…ë£Œ ì‹œê°„ (ë°€ë¦¬ì´ˆ)
 * @returns ë©”ëª¨ë¦¬ ì‚¬ìš© í†µê³„
 */
export async function analyzeMemoryUsage(
  startTime: number = Date.now() - 24 * 60 * 60 * 1000, // ê¸°ë³¸: 24ì‹œê°„
  endTime: number = Date.now()
): Promise<MemoryUsageStats> {
  // ì§€ì •ëœ ê¸°ê°„ì˜ ë¡œê·¸ ê°€ì ¸ì˜¤ê¸°
  const logs = await getMemoryLogs(1000, startTime, endTime);

  if (logs.length === 0) {
    throw new Error('ë¶„ì„í•  ë©”ëª¨ë¦¬ ë¡œê·¸ê°€ ì—†ìŠµë‹ˆë‹¤');
  }

  // ê¸°ë³¸ í†µê³„ ê³„ì‚°
  const usages = logs.map(log => log.info.heap_used_mb || 0);
  const avgUsage = usages.reduce((sum, usage) => sum + usage, 0) / usages.length;
  const peakUsage = Math.max(...usages);
  const minUsage = Math.min(...usages);
  const lastUsage = usages[0]; // ìµœì‹  ë¡œê·¸ê°€ ì²« ë²ˆì§¸

  // ì‹œê°„ë³„ ì‚¬ìš©ëŸ‰
  const usageOverTime = logs.map(log => ({
    timestamp: log.timestamp,
    usageMB: log.info.heap_used_mb || 0
  })).sort((a, b) => a.timestamp - b.timestamp);

  // ìµœì í™” ì´ë²¤íŠ¸ ì¶”ì¶œ
  const optimizationEvents = logs
    .filter(log => log.eventType === MemoryEventType.OPTIMIZATION)
    .map(log => {
      // ìµœì í™” ì „í›„ ë©”ëª¨ë¦¬ ì°¨ì´ ê³„ì‚° (ì´ì „ ë¡œê·¸ì™€ ë¹„êµ)
      const index = logs.indexOf(log);
      const prevLog = logs[index + 1]; // ì—­ìˆœ ì •ë ¬ì´ë¯€ë¡œ ë‹¤ìŒ ì¸ë±ìŠ¤ê°€ ì´ì „ ë¡œê·¸
      const freedMemory = prevLog
        ? (prevLog.info.heap_used_mb || 0) - (log.info.heap_used_mb || 0)
        : 0;

      return {
        timestamp: log.timestamp,
        freedMemory: Math.max(0, freedMemory) // ìŒìˆ˜ì¸ ê²½ìš° 0ìœ¼ë¡œ ì²˜ë¦¬
      };
    });

  // GC ì´ë²¤íŠ¸ ì¶”ì¶œ
  const gcEvents = logs
    .filter(log => log.eventType === MemoryEventType.GARBAGE_COLLECTION)
    .map(log => {
      // GC ì „í›„ ë©”ëª¨ë¦¬ ì°¨ì´ ê³„ì‚°
      const index = logs.indexOf(log);
      const prevLog = logs[index + 1];
      const freedMemory = prevLog
        ? (prevLog.info.heap_used_mb || 0) - (log.info.heap_used_mb || 0)
        : 0;

      return {
        timestamp: log.timestamp,
        freedMemory: Math.max(0, freedMemory)
      };
    });

  // ë©”ëª¨ë¦¬ ëˆ„ìˆ˜ ì˜ì‹¬ ì»´í¬ë„ŒíŠ¸ ë¶„ì„
  const componentMounts = logs.filter(log =>
    log.eventType === MemoryEventType.COMPONENT_MOUNT && log.componentId
  );

  const componentCounts: Record<string, number> = {};
  for (const log of componentMounts) {
    const id = log.componentId!;
    componentCounts[id] = (componentCounts[id] || 0) + 1;
  }

  // ëˆ„ìˆ˜ ì˜ì‹¬ ì»´í¬ë„ŒíŠ¸ (ìƒìœ„ 10ê°œ)
  const leakSuspects = Object.entries(componentCounts)
    .map(([componentId, frequency]) => ({ componentId, frequency }))
    .sort((a, b) => b.frequency - a.frequency)
    .slice(0, 10);

  return {
    averageUsage: avgUsage,
    peakUsage,
    minUsage,
    lastUsage,
    usageOverTime,
    optimizationEvents,
    gcEvents,
    leakSuspects
  };
}

/**
 * ë©”ëª¨ë¦¬ ë¡œê·¸ ë‚´ë³´ë‚´ê¸° (JSON)
 * @returns JSON ë¬¸ìì—´
 */
export async function exportMemoryLogs(): Promise<string> {
  const logs = await getMemoryLogs(MAX_LOG_ENTRIES);
  const performanceData = getPerformanceHistory();

  const exportData = {
    logs,
    performanceHistory: performanceData,
    exportTimestamp: Date.now(),
    appVersion: '1.0.0', // ì•± ë²„ì „
    systemInfo: {
      userAgent: navigator.userAgent,
      platform: navigator.platform,
      language: navigator.language
    }
  };

  return JSON.stringify(exportData, null, 2);
}

/**
 * ë©”ëª¨ë¦¬ ìë™ ë¡œê·¸ ì„¤ì •
 * ì£¼ìš” ì´ë²¤íŠ¸ì— ëŒ€í•œ ë©”ëª¨ë¦¬ ë¡œê¹…ì„ ìë™í™”í•©ë‹ˆë‹¤.
 */
export function setupAutomaticMemoryLogging(): () => void {
  const cleanupFunctions: Array<() => void> = [];

  // ì£¼ê¸°ì  ëª¨ë‹ˆí„°ë§ ì‹œì‘
  const stopMonitoring = startMemoryMonitoring(60000); // 1ë¶„ë§ˆë‹¤
  cleanupFunctions.push(stopMonitoring);

  // í˜ì´ì§€ íƒìƒ‰ ì¶”ì 
  if (typeof window !== 'undefined') {
    // í˜ì´ì§€ ë¡œë“œ ì‹œ
    const handleLoad = () => {
      logMemoryUsage(MemoryEventType.PAGE_NAVIGATION, 'Page loaded');
    };
    window.addEventListener('load', handleLoad);
    cleanupFunctions.push(() => window.removeEventListener('load', handleLoad));

    // í˜ì´ì§€ ì–¸ë¡œë“œ ì‹œ
    const handleBeforeUnload = () => {
      logMemoryUsage(MemoryEventType.PAGE_NAVIGATION, 'Page unloaded');
    };
    window.addEventListener('beforeunload', handleBeforeUnload);
    cleanupFunctions.push(() => window.removeEventListener('beforeunload', handleBeforeUnload));

    // ê°€ì‹œì„± ë³€ê²½ ì‹œ
    const handleVisibilityChange = () => {
      logMemoryUsage(
        MemoryEventType.PAGE_NAVIGATION,
        `Visibility changed: ${document.visibilityState}`
      );
    };
    document.addEventListener('visibilitychange', handleVisibilityChange);
    cleanupFunctions.push(() => document.removeEventListener('visibilitychange', handleVisibilityChange));
  }

  // ëª¨ë“  ì •ë¦¬ í•¨ìˆ˜ë¥¼ í˜¸ì¶œí•˜ëŠ” í•¨ìˆ˜ ë°˜í™˜
  return () => {
    cleanupFunctions.forEach(cleanup => cleanup());
  };
}
