/**
 * ë¡œì»¬ ì €ì¥ì†Œì˜ ë¡œê·¸ë¥¼ ëª¨ë‘ ì„œë²„ë¡œ ë™ê¸°í™”í•©ë‹ˆë‹¤.
 */
export async function syncLocalLogs(): Promise<void> {
  try {
    // ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì—ì„œ ë¡œê·¸ë¡œ ì‹œì‘í•˜ëŠ” í‚¤ ëª¨ë‘ ê°€ì ¸ì˜¤ê¸°
    const logKeys = Object.keys(localStorage).filter(key => key.startsWith('log_'));

    if (logKeys.length === 0) {
      logger.debug('ë™ê¸°í™”í•  ë¡œì»¬ ë¡œê·¸ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    logger.info(`${logKeys.length}ê°œì˜ ë¡œì»¬ ë¡œê·¸ ë™ê¸°í™” ì‹œì‘`);

    // ê° ë¡œê·¸ ë™ê¸°í™”
    for (const key of logKeys) {
      try {
        const logJson = localStorage.getItem(key);
        if (!logJson) continue;

        const log = JSON.parse(logJson);
        await saveLog(log);

        // ì„±ê³µì ìœ¼ë¡œ ë™ê¸°í™”ëœ ë¡œê·¸ëŠ” ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì—ì„œ ì œê±°
        localStorage.removeItem(key);
      } catch (error) {
        logger.error(`ë¡œê·¸ ë™ê¸°í™” ì¤‘ ì˜¤ë¥˜ (${key}):`, toSafeRecord(error));
      }
    }

    logger.info('ë¡œì»¬ ë¡œê·¸ ë™ê¸°í™” ì™„ë£Œ');
  } catch (error) {
    logger.error('ë¡œê·¸ ë™ê¸°í™” ì¤‘ ì˜¤ë¥˜ ë°œìƒ:', toSafeRecord(error));
    throw error;
  }
}

/**
 * ë¡œê¹… ìœ í‹¸ë¦¬í‹°
 */

export enum LogLevel {
  Debug = 'debug',
  Info = 'info',
  Warn = 'warn',
  Error = 'error',
  Critical = 'critical'
}

export function isDebugMode(): boolean {
  return process.env.NODE_ENV === 'development' ||
    (typeof localStorage !== 'undefined' && localStorage.getItem('debug_mode') === 'true');
}

export function addLogEntry(entry: Omit<LogEntry, 'id'>): void {
  try {
    if (typeof localStorage === 'undefined') return;

    const logEntry = {
      ...entry,
      id: Date.now().toString() + Math.random().toString(36).substr(2, 5)
    };

    const logsJson = localStorage.getItem('application_logs');
    const logs = logsJson ? JSON.parse(logsJson) : [];

    logs.push(logEntry);

    if (logs.length > 1000) {
      logs.splice(0, logs.length - 1000);
    }

    localStorage.setItem('application_logs', JSON.stringify(logs));
  } catch (error) {
    console.error('ë¡œê·¸ í•­ëª© ì €ì¥ ì‹¤íŒ¨:', error);
  }
}

export function logDebug(message: string, data?: unknown): void {
  if (!isDebugMode()) return;

  const safeData = toSafeRecord(data);

  console.debug(`[Debug] ${message}`, safeData);

  addLogEntry({
    type: LogType.SYSTEM,
    level: LogLevel.Debug,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  });
}

export function logInfo(message: string, data?: unknown): void {
  const safeData = toSafeRecord(data);

  console.info(`[Info] ${message}`, safeData);

  addLogEntry({
    type: LogType.SYSTEM,
    level: LogLevel.Info,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  });
}

export function logWarn(message: string, data?: unknown): void {
  const safeData = toSafeRecord(data);

  console.warn(`[Warning] ${message}`, safeData);

  addLogEntry({
    type: LogType.SYSTEM,
    level: LogLevel.Warn,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  });
}

export function logError(message: string, data?: unknown): void {
  const safeData = toSafeRecord(data);

  console.error(`[Error] ${message}`, safeData);

  addLogEntry({
    type: LogType.ERROR,
    level: LogLevel.Error,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  });
}

export function logCritical(message: string, data?: unknown): void {
  const safeData = toSafeRecord(data);

  console.error(`[CRITICAL] ${message}`, safeData);

  addLogEntry({
    type: LogType.ERROR,
    level: LogLevel.Critical,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  });
}

function getCurrentSessionId(): string | undefined {
  if (typeof localStorage === 'undefined') return undefined;

  const sessionId = localStorage.getItem('session_id');
  return sessionId || undefined;
}

export function createLogEntry(level: LogLevel, message: string, data?: unknown): Omit<LogEntry, 'id'> {
  const safeData = toSafeRecord(data);

  return {
    type: level === LogLevel.Error || level === LogLevel.Critical ? LogType.ERROR : LogType.SYSTEM,
    level,
    message,
    data: safeData,
    timestamp: Date.now(),
    sessionId: getCurrentSessionId(),
    content: message
  };
}
